<template>
  <div class="view-container">

    <!---->
    <div class="row breadcrumb-container">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{path:'/bm'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="main-page">
      <!--  -->
      <div>
        <el-button type="success" size="small" @click="addPermission()"></el-button>
        <Search v-on:search="getPermissionData()"></Search>
      </div>

      <!--  -->
      <div class="row">
        <el-table :data="permissionData" border style="width: 100%">
          <el-table-column label="ID" prop="id">
          </el-table-column>
          <el-table-column label="" prop="code">
          </el-table-column>
          <el-table-column label="" prop="name">
          </el-table-column>
          <el-table-column label="" prop="createTime" width="96">
            <template slot-scope="scope">
              <div>{{moment(scope.row.createTime)}}</div>
            </template>
          </el-table-column>
          <el-table-column label="" prop="updateTime" width="96">
            <template slot-scope="scope">
              <div v-if="scope.row.updateTime">{{moment(scope.row.updateTime)}}</div>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">

              <el-button type="primary" size="mini" @click="editPermissionItem(scope.row)">
                
              </el-button>
              <el-button type="danger" size="mini" @click="delPermissionItem(scope.row)">
               
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!--  -->
      <div class="row">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalSize">
        </el-pagination>
      </div>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="addPermissionDialogVisible" width="30%" :before-close="handleClose">
      <el-form label-position="left" label-width="80px" :model="addPermissionData">
        <el-form-item label="" prop="code">
          <el-input v-model="addPermissionData.code"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="addPermissionData.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="action">
          <el-input v-model="addPermissionData.action"></el-input>
        </el-form-item>
        <el-form-item label="" prop="menuId">
          <el-select v-model="addPermissionData.menuId" placeholder="">
            <el-option v-for="item in allLeafData" :key="item.id" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="idOnPage">
          <el-input v-model="addPermissionData.idOnPage"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="addPermissionDialogVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="submitAddPermission()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="delPermissionDialogVisible" width="30%" :before-close="handleClose">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="delPermissionDialogVisible = false"> </el-button>
        <el-button type="danger" size="small" @click="submitDelPermission()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editPermissionDialogVisible" width="30%" :before-close="handleClose">
      <el-form label-position="left" label-width="80px" :model="editPermissionData">
        <el-form-item label="" prop="code">
          <el-input v-model="editPermissionData.code"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="editPermissionData.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="action">
          <el-input v-model="editPermissionData.action"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="editPermissionDialogVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="submitEditPermission()"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>
<script>
import Search from './Commen/Search.vue'
import { apiStatusFn } from '../../utils/base.js'
import baseService from '../../service/baseService.js'
import moment from 'moment'

export default {
  name: 'permission',
  data() {
    return {
      // 
      addPermissionDialogVisible: false,
      // 
      addPermissionData: {
        code: '',
        name: '',
        action: '',
        menuId: '',
        idOnPage: ''
      },
      //  
      delPermissionDialogVisible: false,
      // id
      delPermissionId: 0,
      //  
      editPermissionDialogVisible: false,
      //  
      editPermissionData: {
        id: '',
        code: '',
        name: '',
        action: ''
      },
      //
      currentPage: 1,
      pageSize: 20,
      totalSize: 0,
      // 
      permissionData: [],
      // 
      allLeafData: [],
      // 
      tabMaxHeight: 0,
      permissionPath: {
        getPath: '/authority/page',
        addPath: '/authority/add',
        editPath: '/authority/update',
        delPath: '/authority/delete'
      },
      menuPath: {
        allLeafPath: '/menu/all-leaf'
      }
    }
  },
  computed: {
    searchId() {
      return this.$store.state.user.searchId
    }
  },
  components: {
    Search
  },
  mounted() {
    // 
    this.getPermissionData()
  },
  methods: {
    // 
    moment(time) {
      return moment(time).format('YYYY-MM-DD hh:mm:ss')
    },
    //  
    getPermissionData() {
      baseService
        .basePostData(this.permissionPath.getPath, {
          pageNum: this.currentPage,
          pageSize: this.pageSize,
          name: this.searchId
        })
        .then(res => {
          let result = res.data
          this.permissionData = result.data.list
          this.totalSize = result.data.total
          let clientHieght = document.body.clientHeight
          this.tabMaxHeight = clientHieght - 60 - 30 - 30 - 50 - 50
          if (result.status !== '0x0000') {
            this.$message({
              message: result.message,
              type: 'warning'
            })
          }
        })
    },
    //  
    addPermission() {
      this.addPermissionData = {
        code: '',
        name: '',
        action: '',
        menuId: '',
        idOnPage: ''
      }
      this.addPermissionDialogVisible = true
      // 
      baseService
        .basePostData(this.menuPath.allLeafPath)
        .then(res => {
          let result = res.data.data
          this.allLeafData = result
        })
        .catch(err => {
          throw err
        })
    },
    // 
    submitAddPermission(formName) {
      this.addPermissionDialogVisible = false
      baseService.basePostData(this.permissionPath.getPath, this.addPermissionData).then(res => {
        let result = res.data
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
        this.getPermissionData()
      })
    },
    // 
    delPermissionItem(row) {
      this.delPermissionId = row.id
      this.delPermissionDialogVisible = true
    },
    // 
    submitDelPermission() {
      this.delPermissionDialogVisible = false
      baseService.baseGetData(this.permissionPath.delPath, this.delPermissionId).then(res => {
        let result = res.data
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
        this.getPermissionData()
      })
    },
    // 
    editPermissionItem(row) {
      this.editPermissionData.id = row.id
      this.editPermissionData.code = row.code
      this.editPermissionData.name = row.name
      this.editPermissionData.action = row.action
      this.editPermissionDialogVisible = true
      // 
      baseService
        .basePostData(this.menuPath.allLeafPath)
        .then(res => {
          let result = res.data.data
          this.allLeafData = result
        })
        .catch(err => {
          throw err
        })
    },
    // 
    submitEditPermission() {
      this.editPermissionDialogVisible = false
      baseService.basePostData(this.permissionPath.editPath, this.editPermissionData).then(res => {
        let result = res.data
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
        this.getPermissionData()
      })
    },
    // 
    handleClose() {
      this.addPermissionDialogVisible = false
      this.delPermissionDialogVisible = false
      this.editPermissionDialogVisible = false
    },
    // ----------------------------------------------------
    handleSizeChange(val) {
      this.pageSize = val
      this.getPermissionData()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getPermissionData()
    }
  }
}
</script>




