<template>
  <div class="view-container">
    <!---->
    <div class="row breadcrumb-container">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{path:'/bm'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <!--  -->
    <div class="row ope-container">
      <!-- <el-button type="success" size="small" @click="addCategory()"> </el-button> -->
    </div>

    <!--  -->
    <div class="row data-container">
      <el-table :data="categoryDate" border style="width: 100%" :max-height="tabMaxHeight">
        <el-table-column label="ID" prop="id">
        </el-table-column>
        <el-table-column label="" prop="name">
        </el-table-column>
        <!-- <el-table-column label="" prop="">
                </el-table-column> -->
        <!-- <el-table-column label="" prop="">
                </el-table-column> -->
        <!-- <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="danger" size="mini" @click="delCategoryItem(scope.row)">
                            <i class="el-icon-delete"></i>
                        </el-button>
                        <el-button type="primary" size="mini" @click="editCategoryItem(scope.row)">
                            <i class="el-icon-edit"></i>
                        </el-button>
                    </template>
                </el-table-column> -->

      </el-table>
    </div>

    <!--  -->
    <!-- <div class="row page-container">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalSize">
      </el-pagination>
    </div> -->

    <!--  -->
    <el-dialog title="" :visible.sync="addCategoryDialogVisible" width="30%" :before-close="handleClose">
      <el-input v-model="addCategoryDate.name" placeholder=""></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="addCategoryDialogVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="submitAddCategory()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editCategoryDialogVisible" width="30%" :before-close="handleClose">
      <el-input v-model="editCategoryDate.name" placeholder=""></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="editCategoryDialogVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="submitEditCategory()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" class="add-video" :visible.sync="delCategoryDialogVisible" width="30%" :before-close="handleClose">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="delCategoryDialogVisible = false"> </el-button>
        <el-button type="danger" size="small" @click="submitDelCategory()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import baseService from '../../service/baseService.js'
export default {
  name: 'video-list',
  data() {
    return {
      // 
      categoryDate: [],
      tabMaxHeight: 0,
      //   
      addCategoryDialogVisible: false,
      //   
      addCategoryDate: {
        name: ''
      },
      //   
      editCategoryDialogVisible: false,
      //   
      editCategoryDate: {
        name: ''
      },
      //
      delCategoryDialogVisible: false,
      //   id
      delCategoryId: '',

      //
      currentPage: 1,
      pageSize: 20,
      totalSize: 0,

      categoryPath: {
        getPath: '/category/question',
        addPath: '',
        editPath: '',
        delPath: ''
      }
    }
  },
  mounted() {
    this.getCategoryData()
  },
  methods: {
    getCategoryData() {
      baseService.basePostData(this.categoryPath.getPath, {}).then(res => {
        let result = res.data
        this.categoryDate = result.data
        this.totalSize = result.data.total
        let clientHieght = document.body.clientHeight
        this.tabMaxHeight = clientHieght - 60 - 30 - 30 - 50 - 50
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
      })
    },
    //   
    addCategory() {
      this.addCategoryDate = {
        name: ''
      }
      this.addCategoryDialogVisible = true
    },
    // 
    submitAddCategory() {
      this.addCategoryDialogVisible = false
      baseService.basePostData(this.categoryPath.addPath, this.addCategoryDate).then(res => {
        this.getCategoryData()
      })
    },
    //
    delCategoryItem(row) {
      this.delCategoryId = row.id
      this.delCategoryDialogVisible = true
    },
    // 
    submitDelCategory() {
      this.delCategoryDialogVisible = false
      baseService.baseGetData(this.categoryPath.delPath, this.delCategoryId).then(res => {
        this.getCategoryData()
      })
    },
    // 
    editCategoryItem(row) {
      this.editCategoryDate.name = row.name
      this.editCategoryDialogVisible = true
    },
    // 
    submitEditCategory() {
      this.editCategoryDialogVisible = false
      BaseService.basePostData(this.categoryPath.editPath, this.editCategoryDate).then(res => {
        this.getCategoryData()
      })
    },
    // 
    handleClose() {
      this.addCategoryDialogVisible = false
      this.editCategoryDialogVisible = false
      this.delCategoryDialogVisible = false
    },
    // ----------------------------------------------------
    handleSizeChange(val) {
      this.pageSize = val
      this.getCategoryData()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getCategoryData()
    }
  }
}
</script>

<style>
.add-video .el-select {
  width: 100%;
}
</style>




