<template>
  <div class="view-container">

    <!---->
    <div class="row breadcrumb-container">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{path:'/bm'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="main-page">
      <div class=" base-search">
        <label for="">
          <span class="label-span">id</span>
          <el-input size="small" v-model="search.id" placeholder="id"></el-input>
        </label>
        <label for="">
          <span class="label-span"></span>
          <el-input size="small" v-model="search.phone" placeholder=""></el-input>
        </label>
        <label for="">
          <span class="label-span"></span>
          <el-select size="small" v-model="search.disabled">
            <el-option v-for="(item, index) in typeList" :key="index" :label="item.name" :value="item.val">
            </el-option>
          </el-select>
        </label>
        <label for="">
          <span class="label-span"></span>
          <el-select size="small" v-model="search.auditState">
            <el-option v-for="(item, index) in auditList" :key="index" :label="item.name" :value="item.val">
            </el-option>
          </el-select>
        </label>
      </div>
      <div class="row senior-search">
        <label for="">
          <span class="label-span"></span>
          <el-select size="small" v-model="search.idType">
            <el-option v-for="(item, index) in idList" :key="index" :label="item.name" :value="item.val">
            </el-option>
          </el-select>
        </label>
        <label for="">
          <span class="label-span"></span>
          <el-input size="small" v-model="search.idNo" placeholder=""></el-input>
        </label>
      </div>
      <div class="row senior-search">
        <label for="">
          <span class="label-span"></span>
          <el-select size="small" v-model="search.licenseType" placeholder="">
            <el-option v-for="(item, index) in licenseList" :key="index" :label="item.name" :value="item.name">
            </el-option>
          </el-select>
        </label>
        <label for="">
          <span class="label-span"></span>
          <el-input size="small" v-model="search.licenseNo" placeholder=""></el-input>
        </label>
      </div>
      <div class="row senior-search">
        <label for="">
          <span></span>
          <el-date-picker size="small" v-model="creatTime" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="creatTimeChange" value-format="timestamp">
          </el-date-picker>
        </label>
        <label for="">
          <span></span>
          <el-date-picker size="small" v-model="auditTime" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="auditTimeChange" value-format="timestamp">
          </el-date-picker>
        </label>
        <div style="float:right;">
          <el-button size="small" type="primary" @click="getData()"></el-button>
          <el-button size="small" type="primary" @click="reset()"></el-button>
          <el-button size="small" type="primary" @click="add()"></el-button>
        </div>
      </div>

      <!--  -->
      <div class="row">
        <el-table :data="data" border style="width: 100%" min-height="409">
          <el-table-column label="id" prop="id" width="60">
          </el-table-column>
          <el-table-column label="" prop="phone">
          </el-table-column>
          <el-table-column label="" prop="idType" width="90">
            <template slot-scope="scope">
              <div>{{dataIdType[scope.row.idType]}}</div>
            </template>
          </el-table-column>
          <el-table-column label="" prop="idNo">
          </el-table-column>
          <el-table-column label="" prop="licenseType" width="60">
          </el-table-column>
          <el-table-column label="" prop="licenseNo">
          </el-table-column>
          <el-table-column label="" prop="createTime" width="96">
            <template slot-scope="scope">
              <div>{{moment(scope.row.createTime)}}</div>
            </template>
          </el-table-column>
          <el-table-column label="" prop="auditTime" width="96">
            <template slot-scope="scope">
              <div v-if="scope.row.auditTime">{{moment(scope.row.auditTime)}}</div>
            </template>
          </el-table-column>
          <el-table-column label="" prop="auditState" width="70">
            <template slot-scope="scope">
              <div>{{dataAuditStatus[scope.row.auditState]}}</div>
            </template>
          </el-table-column>
          <el-table-column label="" prop="updateTime" width="96">
            <template slot-scope="scope">
              <div v-if="scope.row.updateTime">{{moment(scope.row.updateTime)}}</div>
              <div v-if="!scope.row.updateTime">-</div>
            </template>
          </el-table-column>
          <el-table-column label="" width="150">
            <template slot-scope="scope">
              <div>
                <el-button type="primary" size="mini" @click="detail(scope.row)">
                  
                </el-button>
                <el-button :disabled="scope.row.auditState !== 'INHAND'" type="primary" size="mini" @click="auditItem(scope.row)">
                  
                </el-button>
              </div>
              <div class="row">
                <el-button type="primary" size="mini" @click="edit(scope.row)">
                  
                </el-button>
                <el-button type="primary" size="mini" v-if="!scope.row.disabled" @click="blackItem(scope.row)">
                  
                </el-button>
                <el-button type="primary" size="mini" v-if="scope.row.disabled" @click="whiteItem(scope.row)">
                  
                </el-button>
              </div>

            </template>
          </el-table-column>s
        </el-table>
      </div>
      <!--  -->
      <div class=" page-container">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="search.currentPage" :page-sizes="[10, 50]" :page-size="search.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalSize">
        </el-pagination>
      </div>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="40%" :before-close="handleClose">
      <el-form class="add-form" label-position="left" label-width="0px" :model="addData" :rules="addRules" ref="addFrom">
        <el-form-item label="" prop="idType">
          <label for="" class="driver-label">
            <span class="label-span "></span>
            <el-select size="small" v-model="addData.idType" placeholder="">
              <el-option v-for="(item, index) in idList" :key="index" :label="item.name" :value="item.val">
              </el-option>
            </el-select>
          </label>
        </el-form-item>

        <el-form-item label="" prop="idNo">
          <label for="" class="driver-label">
            <span class="label-span "></span>
            <el-input size="small" v-model="addData.idNo" placeholder=""></el-input>
          </label>
        </el-form-item>

        <el-form-item label="" prop="licenseType">
          <label for="" class="driver-label">
            <span class="label-span "></span>
            <el-select size="small" v-model="addData.licenseType" placeholder="">
              <el-option v-for="(item, index) in licenseList" :key="index" :label="item.name" :value="item.name">
              </el-option>
            </el-select>
          </label>

        </el-form-item>
        <el-form-item label="" prop="licenseNo">
          <label for="" class="driver-label">
            <span class="label-span "></span>
            <el-input size="small" v-model="addData.licenseNo" placeholder=""></el-input>
          </label>

        </el-form-item>
        <el-form-item label="" prop="">
          <label for="" class="driver-label">
            <span class="label-span "></span>
            <el-date-picker size="small" v-model="driverTimeadd" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="driverTimeChangeadd" value-format="timestamp">
            </el-date-picker>
          </label>

        </el-form-item>
        <el-form-item label="" prop="idCardImgUrlToken">
          <el-upload class="upload-demo" action="http://47.95.250.247/admin-api/file/image/upload" :on-success="handleSuccessIdCard" :headers="headers">
            <el-button size="small" type="primary"> </el-button>
          </el-upload>

        </el-form-item>
        <el-form-item label="" prop="headUrlToken">
          <el-upload class="upload-demo" action="http://47.95.250.247/admin-api/file/image/upload" :on-success="handleSuccessHead" :headers="headers">
            <el-button size="small" type="primary"> </el-button>
          </el-upload>

        </el-form-item>
        <el-form-item label="" prop="phone">
          <label for="" class="driver-label">
            <span class="label-span "></span>
            <el-input size="small" v-model="addData.phone" placeholder=""></el-input>
          </label>
        </el-form-item>
         <el-form-item class="btn-">
          <el-button type="primary" size="small" @click="submitAdd('addFrom')"> </el-button>
          <el-button size="small" @click="addDialogVisible = false"> </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="40%" :before-close="handleClose">
      <el-form class="add-form" label-position="left" label-width="0px" :model="editData" :rules="addRules" ref="editFrom">
        <el-form-item label="" prop="idType">
          <label for="" class="driver-label">
            <span class="label-span "></span>
            <el-select size="small" v-model="editData.idType" placeholder="">
              <el-option v-for="(item, index) in idList" :key="index" :label="item.name" :value="item.val">
              </el-option>
            </el-select>
          </label>
        </el-form-item>
        <el-form-item label="" prop="idNo">
          <label for="" class="driver-label">
            <span class="label-span "></span>
            <el-input size="small" v-model="editData.idNo" placeholder=""></el-input>
          </label>
        </el-form-item>
        <el-form-item label="" prop="licenseType">
          <label for="" class="driver-label">
            <span class="label-span "></span>
            <el-select size="small" v-model="editData.licenseType" placeholder="">
              <el-option v-for="(item, index) in licenseList" :key="index" :label="item.name" :value="item.name">
              </el-option>
            </el-select>
          </label>
        </el-form-item>
        <el-form-item label="" prop="licenseNo">
          <label for="" class="driver-label">
            <span class="label-span "></span>
            <el-input size="small" v-model="editData.licenseNo" placeholder=""></el-input>
          </label>
        </el-form-item>
        <el-form-item label="" prop="">
          <label for="" class="driver-label">
            <span class="label-span "></span>
            <el-date-picker size="small" v-model="driverTimeedit" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="driverTimeChangeedit" value-format="timestamp">
            </el-date-picker>
          </label>
        </el-form-item>
        <el-form-item label="" prop="idCardImgUrlToken">
            <el-upload class="upload-demo" action="http://47.95.250.247/admin-api/file/image/upload" :on-success="handleSuccessIdCard" :headers="headers">
            <el-button size="small" type="primary"> </el-button>
          </el-upload>
          
        </el-form-item>
        <el-form-item label="" prop="headUrlToken">
          <el-upload class="upload-demo" action="http://47.95.250.247/admin-api/file/image/upload" :on-success="handleSuccessHead" :headers="headers">
            <el-button size="small" type="primary"> </el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <label for="" class="driver-label">
            <span class="label-span "></span>
            <el-input size="small" v-model="editData.phone" placeholder=""></el-input>
          </label>
        </el-form-item>
        <el-form-item class="btn-">
          <el-button type="primary" size="small" @click="submitEdit('editFrom')"> </el-button>
          <el-button size="small" @click="editDialogVisible = false"> </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="auditDialogVisible" width="30%" :before-close="handleClose">
      <div>
        <el-radio v-model="auditDate.auditState" label="PASSED"></el-radio>
        <el-radio v-model="auditDate.auditState" label="REJECT"></el-radio>
        <el-input v-if="auditDate.auditState === 'REJECT'" v-model="auditDate.auditDesc"></el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="auditDialogVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="submitAudit()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="balckDialogVisible" width="30%" :before-close="handleClose">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="balckDialogVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="submitBlack()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="whiteDialogVisible" width="30%" :before-close="handleClose">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="whiteDialogVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="submitWhite()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import baseService from '../../service/baseService.js'
import { getSessionStorage } from '../../utils/base.js'
import moment from 'moment'
export default {
  name: '',
  data() {
    return {
      headers: {
        authKey: getSessionStorage('authKey')
      },
      tabMaxHeight: 0,
      // api
      apiPath: {
        get: '/driver/page',
        audit: '/driver/audit',
        black: '/driver/ops/black',
        white: '/driver/ops/white',
        add: '/driver/add',
        edit: '/driver/update'
      },
      // 
      data: [],
      // 
      search: {
        pageSize: 20,
        pageNum: 1,
        // id
        id: '',
        // 
        phone: '',
        // 
        auditState: '',
        //
        idType: '',
        //
        idNo: '',
        //
        licenseType: '',
        //
        licenseNo: '',
        //
        createTimeBegin: '',
        // 
        createTimeEnd: '',
        // 
        auditTimeBegin: '',
        // 
        auditTimeEnd: '',
        ///
        disabled: ''
      },
      creatTime: '',
      auditTime: '',
      driverTimeadd: [],
      driverTimeedit: [],
      totalSize: 0,
      // 
      auditDialogVisible: false,
      // 
      auditDate: {
        id: '',
        auditState: '',
        auditDesc: ''
      },
      // 
      balckDialogVisible: false,
      // 
      whiteDialogVisible: false,
      // id
      currentId: '',
      // 
      addDialogVisible: false,
      // 
      addData: {
        idType: '',
        idNo: '',
        licenseType: '',
        licenseNo: '',
        licenseBeginDate: '',
        licenseEndDate: '',
        idCardImgUrlToken: '',
        headUrlToken: '',
        phone: ''
      },
      addRules: {
        idType: [{ required: true, message: '', trigger: 'change' }],
        idNo: [{ required: true, message: '', trigger: 'blur' }],
        licenseType: [{ required: true, message: '', trigger: 'change' }],
        licenseNo: [{ required: true, message: '', trigger: 'blur' }],
        phone: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      editDialogVisible: false,
      // 
      editData: {
        id: '',
        idType: '',
        idNo: '',
        licenseType: '',
        licenseNo: '',
        licenseBeginDate: '',
        licenseEndDate: '',
        idCardImgUrlToken: '',
        headUrlToken: '',
        phone: ''
      }
    }
  },
  computed: {
    typeList() {
      return this.$store.state.data.typeList
    },
    idList() {
      return this.$store.state.data.idList
    },
    licenseList() {
      return this.$store.state.data.licenseList
    },
    auditList() {
      return this.$store.state.data.auditList
    },
    dataIdType() {
      return this.$store.state.data.dataIdType
    },
    dataAuditStatus() {
      return this.$store.state.data.dataAuditStatus
    }
  },
  mounted() {
    // 
    this.getData()
  },
  methods: {
    // 
    moment(time) {
      return moment(time).format('YYYY-MM-DD hh:mm:ss')
    },
    // 
    getData() {
      baseService.basePostData(this.apiPath.get, this.search).then(res => {
        let result = res.data
        this.data = result.data.list
        this.totalSize = result.data.total
        let clientHieght = document.body.clientHeight
        this.tabMaxHeight = clientHieght - 60 - 30 - 117 - 50 - 50
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
      })
    },
    // 
    reset() {
      this.search = {
        // id
        id: '',
        // 
        phone: '',
        // 
        auditState: '',
        //
        idType: '',
        //
        idNo: '',
        //
        licenseType: '',
        //
        licenseNo: '',
        //
        createTimeBegin: '',
        // 
        createTimeEnd: '',
        // 
        auditTimeBegin: '',
        // 
        auditTimeEnd: '',
        ///
        disabled: ''
      }
      this.creatTime = ''
      this.auditTime = ''
      this.getData()
    },
    // 
    add() {
      this.addData = {
        idType: '',
        idNo: '',
        licenseType: '',
        licenseNo: '',
        licenseBeginDate: '',
        licenseEndDate: '',
        idCardImgUrlToken: '',
        headUrlToken: '',
        phone: ''
      }
      this.driverTime = ''
      this.addDialogVisible = true
    },
    // 
    submitAdd(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.addDialogVisible = false
          baseService.basePostData(this.apiPath.add, this.addData).then(res => {
            this.getData()
            let result = res.data
            if (result.status !== '0x0000') {
              this.$message({
                message: result.message,
                type: 'warning'
              })
            } else {
              this.$message({
                message: '',
                type: 'success'
              })
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    edit(row) {
      this.editData.id = row.id
      this.editData.idType = row.idType
      this.editData.idNo = row.idNo
      this.editData.licenseType = row.licenseType
      this.editData.licenseNo = row.licenseNo
      this.editData.licenseBeginDate = row.licenseBeginDate
      this.editData.licenseEndDate = row.licenseEndDate
      this.editData.phone = row.phone 
      this.editDialogVisible = true
      this.driverTimeedit[0] = row.licenseBeginDate
      this.driverTimeedit[1] = row.licenseEndDate
    },
    // 
    submitEdit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.editDialogVisible = false
          baseService.basePostData(this.apiPath.edit, this.editData).then(res => {
            let result = res.data
            if (result.status !== '0x0000') {
              this.$message({
                message: result.message,
                type: 'warning'
              })
            } else {
              this.$message({
                message: '',
                type: 'success'
              })
            }
            this.getData()
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 
    detail(row) {
      this.$router.push(`/driver/detail?id=${row.id}`)
    },
    // 
    auditItem(row) {
      this.auditDate = {
        id: '',
        auditState: '',
        auditDesc: ''
      }
      this.auditDate.id = row.id
      this.auditDialogVisible = true
    },
    // 
    submitAudit() {
      this.auditDialogVisible = false
      if (this.auditDate.auditState === 'PASSED') {
        this.auditDate.auditDesc = ''
      }
      baseService.basePostData(this.apiPath.audit, this.auditDate).then(res => {
        let result = res.data
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
        this.getData()
      })
    },
    // 
    blackItem(row) {
      this.currentId = row.id
      this.balckDialogVisible = true
    },
    // 
    submitBlack() {
      this.balckDialogVisible = false
      baseService.baseGetData(this.apiPath.black, this.currentId).then(res => {
        let result = res.data
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
        this.getData()
      })
    },
    // 
    whiteItem(row) {
      this.currentId = row.id
      this.whiteDialogVisible = true
    },
    // 
    submitWhite() {
      this.whiteDialogVisible = false
      baseService.baseGetData(this.apiPath.white, this.currentId).then(res => {
        let result = res.data
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
        this.getData()
      })
    },
    // 
    handleClose() {
      this.auditDialogVisible = false
      this.balckDialogVisible = false
      this.whiteDialogVisible = false
      this.addDialogVisible = false
      this.editDialogVisible = false
    },
    // ----------------------------------------------------
    handleSizeChange(val) {
      this.search.pageSize = val
      this.getData()
    },
    handleCurrentChange(val) {
      this.search.pageNum = val
      this.getData()
    },
    creatTimeChange(val) {
      this.search.createTimeBegin = val[0]
      this.search.createTimeEnd = val[1]
    },
    auditTimeChange(val) {
      this.search.auditTimeBegin = val[0]
      this.search.auditTimeEnd = val[1]
    },
    driverTimeChangeadd(val) {
      this.addData.licenseBeginDate = val[0]
      this.addData.licenseEndDate = val[1]
    },
    driverTimeChangeedit(val) {
      this.editData.licenseBeginDate = val[0]
      this.editData.licenseEndDate = val[1]
    },
    handleSuccessIdCard(val) {
      this.addData.idCardImgUrlToken = val.data.token
    },
    handleSuccessHead(val) {
      this.addData.headUrlToken = val.data.token
    }
  }
}
</script>





