<template>
  <div class="view-container">
    <!---->
    <div class="row breadcrumb-container">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{path:'/bm'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="main-page">

      <div class="row base-search">
        <label for="">
          <span class="label-span">id</span>
          <el-input size="small" v-model="search.id" placeholder="id"></el-input>
        </label>
        <label for="">
          <span class="label-span"></span>
          <el-input size="small" v-model="search.question" placeholder=""></el-input>
        </label>
      </div>
      <div class="row base-search">
        <label for="">
          <span class="label-span"></span>
          <el-select size="small" v-model="search.subject">
            <el-option v-for="(item, index) in subjectList" :key="index" :label="item.name" :value="item.val">
            </el-option>
          </el-select>
        </label>
        <label for="">
          <span class="label-span"></span>
          <el-select size="small" v-model="search.type">
            <el-option v-for="(item, index) in licenseList" :key="index" :label="item.name" :value="item.name">
            </el-option>
          </el-select>
        </label>
      </div>
      <div class="row senior-search">
        <label for="">
          <span class="label-span"></span>
          <el-date-picker size="small" v-model="creatTime" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="creatTimeChange" value-format="timestamp">
          </el-date-picker>
        </label>
        <label for="">
          <span class="label-span"></span>
          <el-date-picker size="small" v-model="updateTime" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="updateTimeChange" value-format="timestamp">
          </el-date-picker>
        </label>
        <div style="float: right;">
          <el-button size="small" type="primary" @click="getData()"></el-button>
          <el-button size="small" type="primary" @click="reset()"></el-button>
          <el-button size="small" type="primary" @click="add()"></el-button>
        </div>
      </div>

      <!--  -->
      <div class="row ">
        <el-table :data="data" border style="width: 100%">
          <el-table-column label="id" prop="id" width="60">
          </el-table-column>
          <el-table-column label="" prop="phone" width="80">
            <template slot-scope="scope">
              <div v-if="scope.row.subject === '1'"></div>
              <div v-if="scope.row.subject === '4'"></div>
            </template>
          </el-table-column>
          <el-table-column label="" prop="type" width="96">
          </el-table-column>
          <el-table-column label="" prop="licenseType">
            <template slot-scope="scope">
              <el-select size="small" v-model="categoryIds[scope.$index]" placeholder="" @change="change(scope.$index,scope.row)">
                <el-option v-for="(item, index) in categoryDate" :key="index" :label="item.name" :value="item.id">
                </el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column label="" prop="question">
          </el-table-column>
          <el-table-column label="" prop="createTime" width="96">
            <template slot-scope="scope">
              <div>{{moment(scope.row.createTime)}}</div>
            </template>
          </el-table-column>
          <el-table-column label="" prop="updateTime" width="96">
            <template slot-scope="scope">
              <div v-if="scope.row.updateTime">{{moment(scope.row.updateTime)}}</div>
            </template>
          </el-table-column>
          <el-table-column label="" width="150">
            <template slot-scope="scope">
              <div>
                <el-button type="primary" size="mini" @click="detail(scope.row)">
                  
                </el-button>
                <el-button type="primary" size="mini" @click="edit(scope.row)">
                  
                </el-button>
              </div>
              <div class="row">
                <el-button type="danger" size="mini" @click="del(scope.row)">
                  
                </el-button>
              </div>

            </template>
          </el-table-column>s
        </el-table>
      </div>
      <!--  -->
      <div class="row page-container">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="search.pageNum" :page-sizes="[10, 50]" :page-size="search.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalSize">
        </el-pagination>
      </div>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="30%" :before-close="handleClose">
      <el-form class="add-form" label-position="left" label-width="0px" :model="addData" :rules="addRules" ref="addFrom">
        <el-form-item label="" prop="subject">
          <label for="" class="driver-label">
            <span class=""></span>
            <el-select size="small" v-model="addData.subject" placeholder="">
              <el-option v-for="(item, index) in subjectList" :key="index" :label="item.name" :value="item.val">
              </el-option>
            </el-select>
          </label>

        </el-form-item>
        <el-form-item label="" prop="type">
          <label for="">
            <span class=""></span>
            <el-checkbox-group v-model="addData.type">
              <el-checkbox v-for="(item, index) in licenseList" :key="index" :label="item.name"></el-checkbox>
            </el-checkbox-group>
          </label>

        </el-form-item>
        <el-form-item label="" prop="question">
          <label for="">
            <span class=""></span>
            <el-input v-model="addData.question" placeholder="" type="textarea" rows="7"></el-input>
          </label>

        </el-form-item>
        <el-form-item label="" prop="item1">
          <label for="">
            <span class="">A</span>
            <el-input v-model="addData.item1" placeholder="A"></el-input>
          </label>

        </el-form-item>
        <el-form-item label="" prop="item2">
          <label for="">
            <span class="">B</span>
            <el-input v-model="addData.item2" placeholder="B"></el-input>
          </label>

        </el-form-item>
        <el-form-item label="" prop="item3">
          <label for="">
            <span class="">C</span>
            <el-input v-model="addData.item3" placeholder="C"></el-input>
          </label>

        </el-form-item>
        <el-form-item label="" prop="item4">
          <label for="">
            <span class="">D</span>
            <el-input v-model="addData.item4" placeholder="D"></el-input>
          </label>

        </el-form-item>
        <el-form-item label="" prop="answer">
          <label for="">
            <span class=""></span>
            <el-select size="small" v-model="addData.answer" placeholder="">
              <el-option v-for="(item, index) in answerList" :key="index" :label="item" :value="item">
              </el-option>
            </el-select>
          </label>

        </el-form-item>
        <el-form-item label="" prop="explains">
          <label for="">
            <span class=""></span>
            <el-input v-model="addData.explains" placeholder="" type="textarea" rows="7"></el-input>
          </label>

        </el-form-item>
        <el-form-item>
          <el-button size="small" @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" size="small" @click="submitAdd('addFrom')"> </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="30%" :before-close="handleClose">
      <el-form class="add-form" label-position="left" label-width="0px" :model="editData" :rules="addRules" ref="editData">
        <el-form-item label="" prop="subject">
          <label for="" class="driver-label">
            <span class=""></span>
          <el-select size="small" v-model="editData.subject" placeholder="">
           <el-option v-for="(item, index) in subjectList" :key="index" :label="item.name" :value="item.val">
            </el-option>
          </el-select>
          </label>
        </el-form-item>
        <el-form-item label="" prop="type">
          <label for="">
            <span class=""></span>
          <el-checkbox-group v-model="editData.type">
            <el-checkbox v-for="(item, index) in licenseList" :key="index" :label="item.name"></el-checkbox>
          </el-checkbox-group>
           </label>
        </el-form-item>
        <el-form-item label="" prop="question">
           <label for="">
            <span class=""></span>
          <el-input v-model="editData.question" placeholder="" type="textarea" rows="7"></el-input>
        </label>
        </el-form-item>
        <el-form-item label="" prop="item1">
            <label for="">
            <span class="">A</span>
          <el-input v-model="editData.item1" placeholder="A"></el-input>
          </label>
        </el-form-item>
        <el-form-item label="" prop="item2">
          <label for="">
            <span class="">B</span>
          <el-input v-model="editData.item2" placeholder="B"></el-input>
           </label>
        </el-form-item>
        <el-form-item label="" prop="item3">
          <label for="">
            <span class="">C</span>
          <el-input v-model="editData.item3" placeholder="C"></el-input>
          </label>
        </el-form-item>
        <el-form-item label="" prop="item4">
          <label for="">
            <span class="">D</span>
          <el-input v-model="editData.item4" placeholder="D"></el-input>
          </label>
        </el-form-item>
        <el-form-item label="" prop="answer">
          <label for="">
            <span class=""></span>
          <el-select size="small" v-model="editData.answer" placeholder="">
            <el-option v-for="(item, index) in answerList" :key="index" :label="item" :value="item">
            </el-option>
          </el-select>
           </label>
        </el-form-item>
        <el-form-item label="" prop="explains">
           <label for="">
            <span class=""></span>
          <el-input v-model="editData.explains" placeholder="" type="textarea" rows="7"></el-input>
           </label>
        </el-form-item>
        <el-form-item>
          <el-button size="small" @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" size="small" @click="submitEdit('editData')"> </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="delDialogVisible" width="30%" :before-close="handleClose">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="delDialogVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="submitDel()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import baseService from '../../service/baseService.js'
import moment from 'moment'
export default {
  name: '',
  data() {
    return {
      // api
      apiPath: {
        get: '/question/page',
        add: '/question/add',
        edit: '/question/update',
        del: '/question/delete'
      },
      // 
      data: [],
      //   
      search: {
        id: null,
        subject: null,
        type: null,
        question: null,
        createTimeBegin: null,
        createTimeEnd: null,
        updateTimeBegin: null,
        updateTimeEnd: null,
        pageNum: 1,
        pageSize: 20
      },
      totalSize: 0,
      //   
      addDialogVisible: false,
      //   
      addData: {
        subject: '',
        type: [],
        question: '',
        item1: '',
        item2: '',
        item3: '',
        item4: '',
        answer: '',
        explains: ''
      },
      // 
      addRules: {
        subject: [{ required: true, message: '', trigger: 'change' }],
        type: [{ required: true, message: '', trigger: 'change' }],
        question: [{ required: true, message: '', trigger: 'change' }],
        item1: [{ required: true, message: 'A', trigger: 'change' }],
        item2: [{ required: true, message: 'B', trigger: 'change' }],
        answer: [{ required: true, message: '', trigger: 'change' }],
        explains: [{ required: true, message: '', trigger: 'change' }]
      },
      // 
      editDialogVisible: false,
      // 
      editData: {
        id: '',
        subject: 0,
        type: [],
        question: '',
        item1: '',
        item2: '',
        item3: '',
        item4: '',
        answer: '',
        explains: '',
        categoryIds: ''
      },
      // 
      subjectList: [
        {
          name: '',
          val: '1'
        },
        {
          name: '',
          val: '4'
        }
      ],
      // 
      answerList: ['A', 'B', 'C', 'D'],
      tabMaxHeight: 0,
      // 
      delDialogVisible: false,
      // id
      delId: '',
      // 
      categoryDate: [],
      categoryPath: {
        getPath: '/category/question'
      },
      categoryIds: [],
      creatTime: '',
      updateTime: ''
    }
  },
  computed: {
    licenseList() {
      return this.$store.state.data.licenseList
    }
  },
  mounted() {
    // 
    this.getData()
  },
  methods: {
    // 
    moment(time) {
      return moment(time).format('YYYY-MM-DD hh:mm:ss')
    },
    // 
    getData() {
      baseService
        .basePostData(this.apiPath.get, {
          id: this.search.id || null,
          subject: this.search.subject || null,
          type: this.search.type || null,
          question: this.search.question || null,
          createTimeBegin: this.search.createTimeBegin || null,
          createTimeEnd: this.search.createTimeEnd || null,
          updateTimeBegin: this.search.updateTimeBegin || null,
          updateTimeEnd: this.search.updateTimeEnd || null,
          pageNum: this.search.pageNum,
          pageSize: this.search.pageSize
        })
        .then(res => {
          this.data = []
          let result = res.data
          this.data = result.data.list
          this.totalSize = result.data.total
          this.categoryIds = []
          for (let i = 0; i < this.data.length; i++) {
            this.categoryIds.push(this.data[i].categoryId)
          }
          let clientHieght = document.body.clientHeight
          this.tabMaxHeight = clientHieght - 60 - 30 - 117 - 50 - 50
          if (result.status !== '0x0000') {
            this.$message({
              message: result.message,
              type: 'warning'
            })
          }
        })

      baseService.basePostData(this.categoryPath.getPath, {}).then(res => {
        let result = res.data.data
        this.categoryDate = result
      })
    },
    // 
    reset() {
      this.search = {
        id: null,
        subject: null,
        type: null,
        question: null,
        createTimeBegin: null,
        createTimeEnd: null,
        updateTimeBegin: null,
        updateTimeEnd: null,
        pageNum: this.search.pageNum,
        pageSize: this.search.pageSize
      }
      this.creatTime = ''
      this.updateTime = ''
      this.getData()
    },
    detail(row) {
      this.$router.push(`/question/detail?id=${row.id}`)
    },
    // 
    add() {
      this.addDialogVisible = true
    },
    // 
    submitAdd(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.addDialogVisible = false
          this.addData.type = this.addData.type.join(',')
          baseService.basePostData(this.apiPath.add, this.addData).then(res => {
            let result = res.data
            if (result.status !== '0x0000') {
              this.$message({
                message: result.message,
                type: 'warning'
              })
            }
            this.getData()
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 
    edit(row) {
      this.editData.id = row.id
      this.editData.subject = row.subject
      this.editData.type = row.type.split(',')
      this.editData.question = row.question
      this.editData.item1 = row.item1
      this.editData.item2 = row.item2
      this.editData.item3 = row.item3
      this.editData.item4 = row.item4
      this.editData.answer = row.answer
      this.editData.explains = row.explains

      this.editDialogVisible = true
    },
    // 
    submitEdit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.editDialogVisible = false
          this.editData.type = this.editData.type.join(',')

          baseService.basePostData(this.apiPath.edit, this.editData).then(res => {
            let result = res.data
            if (result.status !== '0x0000') {
              this.$message({
                message: result.message,
                type: 'warning'
              })
            }
            this.getData()
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 
    del(row) {
      this.delId = row.id
      this.delDialogVisible = true
    },
    // 
    submitDel() {
      this.delDialogVisible = false
      baseService.baseGetData(this.apiPath.del, this.delId).then(res => {
        let result = res.data
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
        this.getData()
      })
    },
    // 
    change(index, row) {
      this.editData = row
      this.editData.categoryId = this.categoryIds[index]
      baseService.basePostData(this.apiPath.edit, this.editData).then(res => {
        let result = res.data
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
        this.getData()
      })
    },
    handleClose() {
      this.addDialogVisible = false
      this.editDialogVisible = false
      this.delDialogVisible = false
      this.getData()
    },
    // ----------------------------------------------------
    handleSizeChange(val) {
      this.search.pageSize = val
      this.getData()
    },
    handleCurrentChange(val) {
      this.search.pageNum = val
      this.getData()
    },
    creatTimeChange(val) {
      this.search.createTimeBegin = val[0]
      this.search.createTimeEnd = val[1]
    },
    updateTimeChange(val) {
      this.search.updateTimeBegin = val[0]
      this.search.updateTimeEnd = val[1]
    }
  }
}
</script>




